export default{
	props: {
		showTextarea:{
			type: Boolean,
			default: false
		},
	      /**题目对象 */
	      item: {
	        type: Object,
	        default: function() {
	          return {};
	        }
	      },
		  /**是否是组合题 */
		  isCombinationQuestion: {
		    type: Boolean,
		    default: false
		  },
		  /**小题下标*/
		  index:Number,
		  /**父级tkid */
		  parenttkid: {
		    type: Number,
		    default: 0
		  }
	    },
		data(){
			return {
				
			}
		},
	    methods: {
	      handleBlur: function () {
	        this.$emit("callback", this.item,this.isCombinationQuestion, this.parenttkid, this.index);
	      }
	    }
}
